.app {
    position: absolute;
    width: 400px;
    height: 100px;
    border: 1px solid;
    margin: 10px;
    top: 0px;
    bottom: 0px;
    margin: auto;
    left: 0px;
    right: 0px;
    padding: 10px;
    border-radius: 10px;
  }
  
  .app > textarea {
    padding: 10px;
    height: 70px;
    box-sizing: border-box;
    width: 100%;
    outline: none;
    resize: none;
    font-size: 14px;
  }
  
  #char-count {
    position: absolute;
    left: 5px;
    margin: 5px;
    font-size: 12px;
    font-weight: bold;
    background-color: rgb(250, 220, 156);
    padding: 5px;
    width: 110px;
    text-align: center;
    border-radius: 5px;
  }
  
  #word-count {
    position: absolute;
    left: 130px;
    padding: 5px;
    margin: 5px;
    font-size: 12px;
    font-weight: bold;
    background-color: rgb(176, 156, 250);
    width: 110px;
    text-align: center;
    border-radius: 5px;
    color: rgb(255, 255, 255);
  }
  